<template lang="pug">
.contentContainer
    h1.TitleU {{name}}
    .HTMLContainer(v-html="body")
</template>

<script>

export default {
    components: {

    },
    data () {
        return {
            name: "ABOUT THE WORKSHOP",
            body: '<p dir="ltr" style="line-height: 1.7; margin-top: 0pt; margin-bottom: 0pt;"><span style="font-size: 19px; color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">In order to gather research momentum from the UbiComp community, we call for papers discussing how to design or incorporate different input modalities or sensations for enabling a more immersive VR/AR experience. We arrange sessions for each accepted paper to be presented in the workshop. At the end of each session, we will assign session chairs to mediate the discussion to distill motivated ideas and findings to the participants.&nbsp;</span></p><br><p dir="ltr" style="line-height: 1.7; margin-top: 0pt; margin-bottom: 0pt;"><span style="font-size: 19px; color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Besides the regular sessions, we will invite two experts for VR/AR interactions to give talks in the morning and the afternoon, respectively. We hope these two keynote presentations can bring in more interesting perspectives regarding VR/AR-related researches. Finally, a panel discussion will be held in the late afternoon. We will invite 3-5 panelists from both academia and industrial sides to share their valuable experiences related to current research or development trends of VR/AR applications. </span></p>'
        }
    },
    methods: {

    }
}
</script>

<style lang="sass" scoped>
@import "@/style/value.sass"

</style>



