<template lang="pug">
.sraffPage
    h1.TitleU {{name}}
    .staffContainer(v-for='pos in data')
        h1.chairTitle {{pos.title}}
        .staffListContainer
            .staffListContentBlock(v-for='item in pos.staff')
                a.staffListName(:href="item.href" target='_blank') {{item.name}}
                .staffListTitle {{item.agency}}

</template>

<script>
export default {
    components: {
        
    },
    data () {
        return {
            name: "ORGANIZERS",
            data: [
                {title: "Workshop Chairs",
                staff: [
                    {name: 'Chuang-Wen You',agency: 'National Tsing Hua University, Taiwan',href: "https://sites.google.com/site/cwyou2004/"},
                    {name: 'Yi-Chao Chen',agency: 'Shanghai Jiao Tong University, China',href: "http://www.cs.sjtu.edu.cn/~yichao/pmwiki/pmwiki.php"},
                    {name: 'Hsin-Ruey Tsai',agency: 'National Chengchi University, Taiwan',href: "https://hsnuhrt.github.io"},
                    {name: 'Bin Sheng',agency: 'Shanghai Jiao Tong University, China',href: "http://www.cs.sjtu.edu.cn/en/PeopleDetail.aspx?id=149"}
                ]},
                {title: "Technical Program Chair",
                staff: [
                    {name: '',agency: '(To be announced)',href: ""}
                ]},
                {title: "Web Chair",
                staff: [
                    {name: 'Chieh-Jui Ho',agency: 'National Tsing Hua University, Taiwan',href: ""}
                ]}
            ]
        }
    },
    methods: {

    }
}
</script>

<style lang="sass" scoped>
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap')

.TitleU
    text-align: center

    
.staffContainer
    max-width: 1080px
    margin: auto


@media (max-width: 960px)
    .staffListContentBlock
        width: 100%
    .staffListContainer
        width: 100%
    .staffContainer
        flex-flow: wrap
    .staffListContentBlock
        padding-top: 15px
        margin-top: 0 !important
    .chairTitle
        text-align: center
        
.staffListContentBlock
    margin: 30px 
    width: 270px
    margin-left: 40px

.staffListContainer
    display: flex
    flex-flow: wrap
    margin-bottom: 50px
    justify-content: space-between

.staffListImage
    width: 175px
    height: 175px
    border-radius: 8px
    margin: 25%
    background-position: center
.staffListName
    font-size: 19px
    font-weight: bold
    width: 100%
    font-family: 'Roboto', sans-serif
    color: #e8b279
    font-weight: bold
.staffListTitle
    font-size: 19px
    width: 100%
    font-family: 'Roboto', sans-serif
.chairTitle 
    width: 100%
</style>



