<template lang="pug">
.introContainer
    .introImg(:style="{'background-image': 'url(' + src + ')'}")
        .introMask
            .introText {{text1}}
            h1.TitleU {{name}}
            .introText {{text2}}
    
</template>

<script>

export default {
    components: {

    },
    data () {
        return {
            name: "The First Workshop on Multiple Input Modalities and Sensations for VR/AR Interactions (MIMSVAI 2021)",
            src: 'https://mimsvai.github.io/static/imgs/banner.jpg',//image less than 5
            text1: 'September 26, 2021, organised virtually',
            text2: 'Workshop in conjunction with UbiComp 2021'
        }
    },
    methods: {

    }
}
</script>

<style lang="sass" scoped>
.introContainer
    max-width: 1080px
    margin: auto
.introImg
    max-width: 1280px
    margin: auto
    height: 720px
    border-radius: 8px
    position: relative
    // /*opacity: 0.7; optional will put a slide fade on the background images  */
    background-attachment: fixed
    background-position: right
    background-repeat: no-repeat
    background-color: #FFF
    background-size: cover
    z-index: -1
.introText
    font-size: 19px
.introMask
    position: inherit
    padding: 20px
    background-color: #ffffffba
    top: 400px
    z-index: -1


</style>



