<template lang="pug">
#menuContainer
    a#Lego(href='#' v-bind:class="{ 'lego-top': isTop}")
        h1 MIMSVAI 2021
    h1.TitleB {{name}}
    a.menuItem(v-for='item in menuList' :href="'#'+item.name" v-bind:class="{ 'menu-top': isTop}") {{item.name}}
        #underline
    #drawer(v-if="drawerStatus")
        .drawerItem(v-for='(item,i) in menuList' @click='opentheDrawer()') 
            a(:href="'#'+item.name") {{item.name}}
                #underline
    .menuIcon(@click='opentheDrawer()' :class="menuclassList")
        .menuIconBar1
        .menuIconBar2
        .menuIconBar3
</template>

<script>
export default {
    
    data () {
        return {
            iconSrc: 'https://caepo.org/images/menu-logo.svg',
            menuList: [
                {name: "ABOUT"},
                {name: "CALL FOR PAPERS"},
                {name: "ORGANIZER"},
                {name: "AGENDA"},
                {name: "VENUE"}
            ],
            drawerStatus: false,
            menuclassList: {
                menuIconContainer: true,
                change: false
            },
            name: 'Oppps',
            isTop: true
        }
    },
    watch:{
        '$route': function(){
            this.name = this.$route.params.PID;
        }
    },
    mounted() {
        this.name = this.$route.params.PID;
        window.addEventListener('scroll', this.handleScroll)
    },
    methods: {
        opentheDrawer: function(){
            this.drawerStatus = !this.drawerStatus;
            this.menuclassList.change = !this.menuclassList.change;
        },
        handleScroll: function (evt, el) {
            if(window.scrollY>100){
                this.isTop = false;
            }
            else{
                this.isTop = true;
            }
        }
    }
}

</script>

<style lang="sass">
@import "@/style/value.sass"

#Lego
    position: fixed
    left: 40px
    top: -5px
    transition: 0.8s
.lego-top
    top: 10px !important

#menuContainer
    position: fixed
    top: 0px
    right: 0px
    left: 0px
    display: flex
    flex-wrap: nowrap
    justify-content: flex-end
    padding: 10px
    background-color: #ffffffaa

.menuItem
    cursor: pointer
    margin-right: 15px
    margin-top: 10px
    transition: 0.8s
    font-weight: bold
.menu-top
    margin-top: 25px !important

.menuIcon
    cursor: pointer

.menuIconBar1, .menuIconBar2, .menuIconBar3
    width: 35px
    height: 3px
    background-color: $c-text
    transition: 0.3s
    margin: 8px
    position: relative
    border-radius: 25px
.change .menuIconBar1
    opacity: 0
    
.change .menuIconBar2 
    -webkit-transform: rotate(-20deg) translate(-6px, 6px)
    transform: rotate(-20deg)

.change .menuIconBar3 
    -webkit-transform: rotate(225deg) 
    transform: rotate(225deg) translate(8px, 8px)
    transition-delay: 0.2s
    
#drawer
    background-color: #fff
    opacity: 0.9
    position: fixed
    text-align: center
    width: 100%
    right: 0px
    top: 0px
    bottom: 0px
    padding-top: 20%
    
.drawerItem
    cursor: pointer
    padding: 15px

@media all and (min-width: 960px)
    .menuItem
        opacity: 1
    .menuIcon,#drawer
        opacity: 0
        transition: 0.3s
        visibility: hidden
    #menuContainer
        position: fixed
    
@media all and (max-width: 960px)
    #menuContainer
        position: fixed
    .menuItem
        opacity: 0
        transition: 0.3s
        visibility: hidden
    .menuIcon,#drawer
        opacity: 0.9


</style>



