        return {
            name: "CALL FOR PAPERS",
            body: '<p dir="ltr" style="margin-top: 0pt; margin-bottom: 0pt; line-height: 1.7;"><span style="font-size: 19px;  color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">With the advance of VR/AR technology, more and more VR/AR&nbsp;</span><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">applications are emerging and have been popular among new users.&nbsp;</span></span><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">To facilitate user interaction with the systems, multi-modal input&nbsp;</span></span><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">modalities are required. Furthermore, coherent integration of&nbsp;</span></span><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">multiple realistic sensations can increase the level of realism, and&nbsp;</span></span><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">therefore creates a truly immersive VR/AR experience. However,&nbsp;</span></span><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">a lack of robust and intuitive interaction interfaces and realistic&nbsp;</span></span><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">sensations hinders users&rsquo; experience for achieving a fascinating&nbsp;</span></span><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">acceptance in various application areas of VR/AR interactions. This&nbsp;</span></span><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">workshop discusses the challenges and applications of designing a&nbsp;</span></span><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">higher coherence between the different input modalities and sensations&nbsp;</span></span><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">to provide more engaging VR/AR experiences by leveraging&nbsp;</span></span><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">the advances of both VR/AR software and hardware to improve </span></span><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">user experience.</span></span></p><h1 dir="ltr" style="margin-top: 18pt; margin-bottom: 6pt; line-height: 1.7;"><span style="font-size: 30px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Topics</span></span></h1><p dir="ltr" style="margin-top: 0pt; margin-bottom: 0pt; line-height: 1.7;"></p><p><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Papers may include, but not be limited to, topics such as:</span></span></p><ul>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">2D/3D and volumetric display and projection technology</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Immersive analytics and visualization</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Modeling and simulation</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Multimodal capturing and reconstruction</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Scene description and management issues</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Storytelling</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Tracking and sensing</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Audio interfaces, sound rendering, spatialized audio, auditory perception and psychoacoustics</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Embodied agents, virtual humans and (self-)avatars</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Haptic and tactile interfaces, wearable haptics, passive haptics, pseudo haptics, other touch-based UI</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Mediated and diminished reality</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Multimodal input and output</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Multisensory rendering, registration, and synchronization</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Perception and cognition</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Presence, body ownership, and agency</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Teleoperation and telepresence</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">3D user interaction</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">3D UI metaphors</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Collaborative interactions</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Ethical issues</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Human factors and ergonomics</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Input devices</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Locomotion and navigation</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Multimodal/cross-modal interaction and perception</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Non-fatiguing 3D UIs</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Non-visual interfaces (such as olfactory)</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Touch, tangible and gesture interfaces</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Usage research, evaluation methods and empirical studies</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Domain-specific VR/AR applications.</span></span></li>    <li><span style="line-height: 1.7;font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Design process, platform/tools, or libraries used to incorporate input modalities or sensations for VR/AR interactions or studying their use</span></span></li></ul><h1 dir="ltr" style="margin-top: 18pt; margin-bottom: 6pt; line-height: 1.7;"><span style="font-size: 30px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Important Dates</span></span></h1><p dir="ltr" style="margin-top: 0pt; margin-bottom: 0pt; line-height: 1.7;"><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Paper submission: 23:59 (AoE), June 15, 2021</span></span></p><p dir="ltr" style="margin-top: 0pt; margin-bottom: 0pt; line-height: 1.7;"><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Paper notification: 23:59 (AoE), July 15, 2021</span></span></p><p dir="ltr" style="margin-top: 0pt; margin-bottom: 0pt; line-height: 1.7;"><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Camera-ready: 23:59 (AoE), July 31, 2021</span></span></p><p dir="ltr" style="margin-top: 0pt; margin-bottom: 0pt; line-height: 1.7;"><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Workshop: September 26, 2021</span></span></p><h1 dir="ltr" style="margin-top: 18pt; margin-bottom: 6pt; line-height: 1.7;"><span style="font-size: 30px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Best Paper Award</span></span></h1><p dir="ltr" style="margin-top: 0pt; margin-bottom: 0pt; line-height: 1.7;"><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">The Best Paper Award is given to the best paper presented at the MIMSVAI 2021 workshop, to acknowledge and encourage excellence in research. The awardee will be presented with a certificate at the workshop.</span></span></p><h2 dir="ltr" style="margin-top: 18pt; margin-bottom: 6pt; line-height: 1.7;"><span style="font-size: 30px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Submission Guidelines</span></span></h2><p dir="ltr" style="margin-top: 0pt; margin-bottom: 0pt; line-height: 1.7;"><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">Please submit papers with a maximum length of 5 pages (4-page + 1 references) in ACM SIGCHI Master Article template with 2 columns. The accepted papers will be published in the&nbsp;UbiComp/ISWC Adjunct Proceedings, which will be included in the ACM Digital Library. The Best Paper Award is given to the best paper presented at the MIMSVAI 2021 workshop, to acknowledge and encourage excellence in research. The awardee will be presented with a certificate at the workshop.</span></span></p><p style="line-height: 1.7;"><span style="font-size: 19px;"><br></span></p><p dir="ltr" style="margin-top: 0pt; margin-bottom: 0pt; line-height: 1.7;"><span style="font-size: 19px;"><span style=" color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">ACM SIGCHI Master Article template with 2 columns can be found &nbsp;</span><a href="https://iswc.net/iswc21/cfp/template-information/" style="text-decoration:none;"><span style=" color: rgb(17, 85, 204); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: underline; text-decoration-skip-ink: none; vertical-align: baseline; white-space: pre-wrap;">here</span></a>.</span></p><p dir="ltr" style="margin-top: 0pt; margin-bottom: 0pt; line-height: 1.7;"><span style="font-size: 19px;  color: rgb(0, 0, 0); background-color: transparent; font-weight: 400; font-style: normal; font-variant: normal; text-decoration: none; vertical-align: baseline; white-space: pre-wrap;">The accepted papers will be included in the ACM Digital Library as part of the UbiComp conference supplemental proceedings.</span></p>'
        }
    },
    methods: {

    }
}
